
import { VhAlgorithm, VhEcommerce } from 'ionic-vhframeworks';
import { Router } from '@angular/router';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponentShopee implements OnInit {
  bill: any;
  dataRestore: any;
  heightScroll: any;
  widthScroll: any;
  price: number = 0;
  payment_fee = 0;
  infoShop: any = {}
  escrowShopee: any;
  constructor(
    private router: Router,
    private vhEcommerce: VhEcommerce,
    private cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    public vhComponent: VhComponent
  ) {
    this.bill = this.router.getCurrentNavigation().extras.state.bill;
    this.dataRestore = this.router.getCurrentNavigation().extras.state.dataRestore;
    this.infoShop = this.vhEcommerce.getlocalShopListShopee().find(item => item._id == this.dataRestore['id_shop_selected'])
  }

  async ngOnInit() {
    //tính tổng tiền sp
    let sub_total: number = 0
    this.bill['item_list'].forEach(item => {
      sub_total += (item.model_discounted_price || item.model_original_price) * item.model_quantity_purchased
    })
    this.bill['sub_total'] = sub_total

    this.escrowShopee = await this.vhEcommerce.getEscrowShopee(this.bill.order_sn, this.infoShop.access_token, this.infoShop._id);
    console.log(this.escrowShopee);
    console.log(this.bill);

    if(this.bill.order_status === 'CANCELLED') {
      let total_discount_from_voucher_seller = 0;
      let total_discount_from_voucher_shopee = 0;
      let total_discount_from_coin = 0;
      this.escrowShopee?.order_income?.items.forEach(item => {
        total_discount_from_voucher_seller += item.discount_from_voucher_seller;
        total_discount_from_voucher_shopee += item.discount_from_voucher_shopee;
        total_discount_from_coin += item.discount_from_coin;
      });
      this.bill['total_discount_from_voucher_seller'] = total_discount_from_voucher_seller;
      this.bill['total_discount_from_voucher_shopee'] = total_discount_from_voucher_shopee;
      this.bill['total_discount_from_coin'] = total_discount_from_coin;
    }
  }

  ngAfterViewChecked() {
    if (document.querySelector("#order-status-shopee") && document.querySelector(".order-status-shopee-header") && document.querySelector("#bill-info") && document.querySelector("#buyer-info") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#order-status-shopee").clientHeight -
        - document.querySelector(".order-status-shopee-header").clientHeight - document.querySelector("#bill-info").clientHeight - document.querySelector("#buyer-info").clientHeight - 90 + "px";
    }
    if (document.getElementById("order-status-shopee"))
      this.widthScroll = document.getElementById("order-status-shopee").clientWidth + "px"
    this.cdRef.detectChanges();
  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/shopee/issue-invoice/issue-receive-goods'], {
      state: { dataRestore: this.dataRestore }
    })
  }

  gotoPrint() {
    this.router.navigate(["/sales/dashboard/manage/ecommerce/shopee/print"], {
      state: {
        bill: this.bill, dataRestore:
          this.dataRestore,
        path: this.router.url,
        infoShop: this.infoShop
      }
    })
  }
}
